#about-me {
  padding: 2rem 2.5rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 850px;
  margin: 2rem auto;
}

#about-me h2 {
  font-size: 2rem;
  color: brown; 
  font-family: playfair display;
  margin-bottom: 1rem;
  margin-top: 1rem;
  text-align: center;
}

#about-me p {
  font-size: 1.2rem;
  line-height: 1.65;
  color: #555;
  text-align: justify;
  text-justify: inter-word;
  letter-spacing: 0.5px;
  font-family: Playfair display;
}

#about-me p {
  border-left: 5px solid brown;
  padding-left: 1.2rem;
}

.hobby-imgs-container {
  display: flex;
  justify-content: center;
  gap: 18px;
  margin-top: 20px;
  margin-bottom: 80px
}

.hobby-img {
  width: 200px;
  height: auto;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid brown;
  transition: transform 0.3s ease-in-out;
}

@media (max-width: 768px) {
  #about-me {
    padding: 1.5rem;
  }
  
  #about-me h2 {
    font-size: 1.8rem;
  }

  #about-me p {
    font-size: 1rem;
  }

  .hobby-imgs-container {
    flex-direction: column;
    align-items: center;
  }

  .hobby-img {
    width: 150px;
    height: 150px;
  }
}